.custom-card {
    display: flex;
    flex-direction: column;
    background-color: #fcfefe;
    border: 1px solid #eceff3;
    align-items: center;
    padding: 10px 60px;

    margin-bottom: 10px;
}

.custom-card__title {
    font-size: 24px;
    margin-bottom: 10px;
}

.custom-card__img img {
    width: 100%;
    height: auto;
    margin-bottom: 10px;
}

.custom-card__desc {
    margin-bottom: 10px;
}

.custom-card__list {
    list-style-type: none;
    margin: 0;
    padding: 0;

    span {
        font-weight: bold;
    }
}

.custom-card__order-price {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 10px;

    .custom-card__order-price__new-price {
        font-size: 24px;
        font-weight: bold;
    }

    .custom-card__order-price__old-price {
        font-size: 18px;
        text-decoration: line-through;
        margin-right: 15px;
        position: relative;
        color: #8B0000;
        font-weight: bold;
    }
}

.custom-card__btn {
    background: #fee48f;
    background: linear-gradient(180deg,#fee48f 0%,#f9b334 100%);
    border-bottom: 2px solid rgba(255, 150, 0, 0.5);
    border-left: 1px solid rgba(255, 150, 0, 0.5);
    border-right: 1px solid rgba(255, 150, 0, 0.5);
    border-top: 1px solid rgba(255, 150, 0, 0.5);
    color: #7f350c;
    border-radius: 7px;
    text-transform: uppercase;
    padding: 8px 10px;
    transition: .3s;
    display: block;
    position: relative;
    text-align: center;
    font-weight: 700;
    cursor: pointer;
    font-size: 16px;
    width: 100%;
    outline: none;

    &:hover {
        box-shadow: 0 2px 0 0 rgba(255,150,0,.004), 0 10px 35px 0 rgba(255,150,0,.3), inset 0 2px 0 0 rgba(255,254,204,.004);
    }
}

@media (max-width: 576px) {

    .custom-card {
        padding: 10px 10px;
    }

    .custom-card__img img {
        display: block;
        width: 100%;
        height: auto;
    }
}